    <h2>Options</h2>
    <table class="atui-table table-bordered">
      <thead>
        <tr>
          <th>名称</th>
          <th>类型</th>
          <th>默认值</th>
          <th>说明</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>show</td>
          <td><code>Boolean</code></td>
          <td><code>true</code></td>
          <td>是否显示</td>
        </tr>
        <tr>
          <td>dismissable</td>
          <td><code>Boolean</code></td>
          <td><code>false</code></td>
          <td>是否显示关闭按钮</td>
        </tr>
        <tr>
          <td>type</td>
          <td><code>String</code>, one of
          <code>success</code>
          <code>info</code>
          <code>warning</code>
          <code>danger</code>
          <code>help</code>
          </td>
          <td><code>success</code></td>
          <td>组件样式</td>
        </tr>
        <tr>
          <td>duration</td>
          <td><code>Number</code></td>
          <td><code>0</code></td>
          <td>自动关闭时间. 设置为 <code>0</code>或负数将不自动关闭.</td>
        </tr>
        <tr>
          <td>height</td>
          <td><code>String</code></td>
          <td>高度</td>
        </tr>
        <tr>
          <td>width</td>
          <td><code>String</code></td>
          <td>宽度</td>
        </tr>
        <tr>
          <td>arrow</td>
          <td><code>String</code>one of <code>top、left、right、bottom</code></td>
          <td>剪头的方向，如果没有传则不显示箭头，默认不显示</td>
        </tr>
        <tr>
          <td>placement</td>
          <td><code>String</code>. one of <code>top</code>, <code>top-right</code></td>
          <td></td>
          <td>组件的位置</td>
        </tr>
        <tr>
          <td>showIcon</td>
          <td><code>Boolean</code></td>
          <td><code>true</code></td>
          <td>是否显示相应的ICON</td>
        </tr>
      </tbody>
