  </div>
</template>

<script>
  import {Message} from 'src/'
  export default {
    data() {
      return {
        showRight: false,
        showTop: false,
        showCenter:false
      }
    },
    components: {
      Message,
    },
    methods: {
      showMessage() {
        Message['warning']('成功啦', 3000, 'top')
      }
    }
  }
</script>



